import React, { useState, useEffect, useMemo } from "react";
import styled from "@emotion/styled";
// import dayjs from "dayjs";
import { ThemeProvider } from "@emotion/react";
import sunriseAndSunsetData from "./sunrise-sunset.json";
import WeatherCard from "./WeatherCard.js";
import useWeatherApi from "./useWeatherApi.js";
import WeatherSetting from "./WeatherSetting";
import { findLocation } from "./utils";
var dayjs = require('dayjs');
//
const theme = {
  light: {
    backgroundColor: "#EBEBEB",
    foregroundColor: "#EBEBEB",
    boxShadow: "0 1px 3px 0 #999999",
    titleColor: "#000000",
    temperatureColor: "#383838",
    textColor: "#000000"
  },
  dark: {
    backgroundColor: "#323334",
    foregroundColor: "#323334",
    boxShadow:
      "0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15)",
    titleColor: "#ffffff",
    temperatureColor: "#ffffff",
    textColor: "#ffffff"
  }
};
const Container = styled.div`
  background-color: ${({ theme }) => theme.backgroundColor};
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

//
const getMoment = (locationName) => {
  const location = sunriseAndSunsetData.find(
    (data) => data.locationName === locationName
  );
  // console.log("12312313:" + locationName, sunriseAndSunsetData);
  if (!location) return null;
  const now = dayjs();
  //  "2019-10-08" 
  const nowDate = Intl.DateTimeFormat("zh-TW", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit"
  })
    .format(now)
    .replace(/\//g, "-");
  // 
  const locationDate =
    location.time && location.time.find((time) => time.dataTime === nowDate);
  //TimeStamp
  const sunriseTimestamp = dayjs(
    `${locationDate.dataTime} ${locationDate.sunrise}`
  ).unix();
  const sunsetTimestamp = dayjs(
    `${locationDate.dataTime} ${locationDate.sunset}`
  ).unix();
  const nowTimeStamp = now.unix();

  // 
  return sunriseTimestamp <= nowTimeStamp && nowTimeStamp <= sunsetTimestamp
    ? "day"
    : "night";
};

const WeatherApp = () => {
  // console.log("-----before useState");
  // localStorage  cityName storageCity
  const storageCity = localStorage.getItem("cityName");

  // storageCity  currentCity  ''
  const [currentCity, setCurrentCity] = useState(storageCity || "");
  const currentLocation = findLocation(currentCity) || {};

  //  currentPage  state WeatherCard
  const [currentPage, setCurrentPage] = useState("WeatherCard");
  // useState ()
  const [currentTheme, setCurrentTheme] = useState("light");

  // useWeatherApi Hook  weatherElement  fetchData 
  const [weatherElement, fetchData] = useWeatherApi(currentLocation);

  //API
  const moment = useMemo(() => getMoment(currentLocation.cityName), [
    currentLocation.cityName
  ]);

  //  moment 
  useEffect(() => {
    setCurrentTheme(moment === "day" ? "light" : "dark");
  }, [moment]); //  moment  dependencies 
  //  currentCity  localStorage 
  useEffect(() => {
    localStorage.setItem("cityName", currentCity);
  }, [currentCity]);
  return (
    <ThemeProvider theme={theme[currentTheme]}>
      {/* {console.log(currentTheme, moment, currentLocation)} */}
      <Container>
        {currentPage === "WeatherCard" && (
          <WeatherCard
            cityName={currentLocation.cityName}
            weatherElement={weatherElement}
            moment={moment}
            fetchData={fetchData}
            setCurrentPage={setCurrentPage}
          />
        )}

        {currentPage === "WeatherSetting" && (
          <WeatherSetting
            cityName={currentLocation.cityName}
            setCurrentCity={setCurrentCity}
            setCurrentPage={setCurrentPage}
          />
        )}
      </Container>
    </ThemeProvider>
  );
};

export default WeatherApp;

